.search-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form input[type="text"] {
  background-color: #f5f5f5;
  border: none;
  border-radius: 4px;
  padding: 10px 16px;
  font-size: 16px;
}

.search-form input[type="text"]:focus {
  background-color: #fff;
  outline: none;
}

.search-form button[type="submit"] {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-left: 16px;
  padding: 10px 16px;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
}

.search-form button[type="submit"]:hover, .search-form button[type="submit"]:focus {
  background-color: #0069d9;
  outline: none;
}

.load-more {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 16px;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.is-hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 50px;
  display: flex;
}

.photo-card {
  width: calc(25% - 20px);
  border-bottom: 1px solid #ddd;
